.about {
  margin: 110px 0;
}

.about__title {
  font-size: 22px;
  line-height: 27px;
  font-weight: 400;
  margin: 0;
  padding-bottom: 23px;
  border-bottom: 1px solid black;
}
.about__content {
  display: flex;
  flex-direction: row;
  gap: 40px;
  margin: 70px 0 40px;
}

.about__timeline {
  display: flex;
  flex-direction: row;
  margin: 70px 0 40px;
}

.about__contentSubTitle {
  font-size: 20px;
  line-height: 24.2px;
  font-weight: 400;
  margin: 0 0 26px;
}
.about__contentDescription {
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}
.about__timelineGroup {
  max-width: 1140px;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 14px;
  align-items: center;
}

.about__timelineGroup:first-of-type {
  flex-basis: 20%;
}

.about__timelineGroup:last-of-type {
  flex-basis: 80%;
}

.about__timelineBack {
  width: 100%;
  max-width: 228px;
  height: 36px;
  background-color: #3456f3;
  color: white;
  font-size: 14px;
  line-height: 36px;
}

.about__timelineFront {
  width: 100%;
  max-width: 912px;
  height: 36px;
  font-size: 14px;
  line-height: 36px;
  background-color: #f2f2f2;
}

.about__timelineSign {
  font-size: 14px;
  line-height: 17px;
  color: #a0a0a0;
}

@media (max-width: 820px) {
  .about__content {
    gap: 30px;
    margin: 70px 0 20px;
  }
  .about {
    margin: 90px 0;
  }
}

@media (max-width: 550px) {
  .about {
    margin: 70px 0;
  }
  .about__title {
    font-size: 18px;
    line-height: 22px;
  }
  .about__content {
    flex-direction: column;
    gap: 56px;
  }
  .about__contentSubTitle {
    font-size: 18px;
    line-height: 22px;
  }
  .about__contentDescription {
    font-size: 11px;
    line-height: 22px;
  }
}
