.techs {
  padding-top: 100px !important;
  padding-bottom: 100px !important;
  background-color: #f5f5f5;
}

.techs__title {
  font-size: 22px;
  line-height: 27px;
  font-weight: 400;
  margin: 0;
  padding-bottom: 23px;
  border-bottom: 1px solid black;
}

.techs__content {
  text-align: center;
  margin: 90px auto 100px;
  max-width: 460px;

  .techs__main {
    font-size: 50px;
    line-height: 58px;
    font-weight: 400;
    margin: 0 0 26px;
  }
  .techs__description {
    font-size: 14px;
    line-height: 20px;
    margin: 0;
  }
}

.techs__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  list-style-type: none;
  padding: 0;
}

.techs__element {
  list-style: none;
  width: 90px;
  height: 60px;
  background-color: #e8e8e8;
  line-height: 60px;
  text-align: center;
  border-radius: 10px;
  font-size: 14px;
}

@media (max-width: 820px) {
  .techs {
    padding-top: 90px !important;
    padding-bottom: 90px !important;
  }
  .techs__content {
    margin: 80px auto;
  }
}

@media (max-width: 550px) {
  .techs {
    padding-top: 70px !important;
    padding-bottom: 70px !important;
    background-color: #fafafa;
  }
  .techs__title {
    font-size: 18px;
    line-height: 22px;
  }
  .techs__content {
    .techs__main {
      font-size: 30px;
      line-height: 36px;
    }
  }
}
